@mixin ab($t, $l, $b, $r) {
    position: absolute;    
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }
}
@mixin fixed($t, $l, $b, $r) {
    position: fixed;
    @if $l != n { left: $l; }
    @if $t != n { top: $t; }
    @if $r != n { right: $r; }
    @if $b != n { bottom: $b; }    
}
@mixin border-inline-start($value) {
    border-inline-start:$value;
}  
@mixin border-inline-end($value) {
    border-inline-end:$value;
}
@mixin rotate($rotate) { 
  transform: rotate($rotate);
}
@mixin padding-start($value) {
    padding-inline-start: $value;
}
@mixin padding-end($value) {
    padding-inline-end: $value;
}
@mixin margin-start($value) {
    margin-inline-start: $value;
}
@mixin margin-end($value) {
    margin-inline-end: $value;
}
@mixin text-right {
    text-align: end;
}
@mixin text-left {
    text-align: start;
}

.main-product-info {
	.bundle-content {
		.product-reviews-badge {
			.spr-badge {
				margin: 0 0 10px;
			}
		}
	}
}
.bundle-content {
	border: 2px solid var(--secondary-color);
	border-radius: var(--border-radius);
	.bundle-list-item {
		margin: 0;
	}
	.product-reviews-badge {
		.spr-badge {
			margin: 0 0 6px;
		}
	}
	.card {
		padding: 15px;
		margin-bottom: 0;
		border-bottom: 1px solid var(--miscellaneous-color);
		product-card {
			flex-direction: unset;
		}
		header {
			flex: 0 0 90px;
			max-width: 90px;
			@include margin-end(20px);
		}
		.product-price {
			margin-bottom: 5px;
			margin-top: 0;
		}
		.product-weight {
			margin-bottom: 10px;
		}
	}
	.card-group {
		display: none;
	}
	.bundle-item:not(.available) {
		opacity: 0.67;
		pointer-events: none;
	}
	.label-group {
		display: none;
	}
	.select-variants {
		position: relative;
		border-radius: var(--border-radius);
		border: 1px solid var(--miscellaneous-color);
		margin-top: 8px;
		cursor: pointer;
		max-width: 160px;
		select {
			border: 0;
			padding: 3px 10px;
			border-radius: var(--border-radius);
		}
		>svg {
			@include ab(n,n,n,10px);
			height: 100%;
			pointer-events: none;
			path {
				fill: var(--text-color);
			}
		}
	}
}
.section-bundle-list {
	.bundle-cart {
		padding: 15px;
	}
}
.bundle-checklist {
	label {
		margin: 0;
		@include padding-start(30px);
		width: 100%;
		cursor: pointer;
		transition: 0.2s all ease-in-out;
		display: flex;
		flex-wrap: wrap;
		&:before {
			content: "";
			@include ab(0,0,n,n);
			width: 20px;
			height: 20px;
			border: 1px solid #E3E3E3;
			border-radius: 4px;
			transition: 0.25s all ease-in-out;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		>span {
			width: 100%;
		}
	}
	input:checked + label {
		font-weight: 500;
		&:before {
			content: url("data:image/svg+xml,%3Csvg width='10' height='8' viewBox='0 0 10 8' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M9.94175 1.35057L3.53528 7.75704L0 4.20939L1.35293 2.86118L3.53764 5.05353L8.59118 0L9.94175 1.35057Z' fill='white'/%3E%3C/svg%3E%0A");
			background: var(--primary-color);
			border-color: var(--primary-color);
		}
	}
	li {
		line-height: 1.5em;
		font-weight: 400;
		color: var(--text-color);
		position: relative;
		margin: 10px 0;
		cursor: pointer;
		&:hover {
			label {
				color: var(--primary-color);
				&:before {
					border-color: var(--primary-color);
				}
			}
		}
	}
	.product-price {
		font-weight: 500;
		font-size: 16px;
		.price-compare {
			font-weight: 400;
		}
	}
}
.bundle-submit {
	margin-top: 25px;
	.bundle-total-label {
		margin: 0 0 5px;
		font-size: 14px;
	}
	price-item {
		font-size: 36px;
		line-height: 1.1em;
		color: #05a845;
		font-weight: 600;
	}
	button {
		margin-top: 25px;
		height: 46px;
		display: flex;
		align-items: center;
		justify-content: center;
		min-width: 250px;
		width: 100%;
		>span.icon {
			@include margin-end(13px);
			display: flex;
		}
		.icon-loading {
			display: none;
		}
		&.loading {
			.icon-loading {
				display: block;
				width: 20px;
				height: 20px;
				@include margin-end(13px);
				path {
					fill: initial;
				}
			}
			span.icon {
				display: none;
			}
		}
	}
}

.product-layout-bundle {
	.second-column .product-info-inner {
		width: 100%;
	}
	.bundle-title {
		margin-bottom: 15px;
		font-size: 20px;
		font-weight: 500;
	}
	.bundle-content {
		product-card {
			align-items: center;
			card-quick-view {
				button {
					padding: 5px;
				}
				svg {
					width: 14px;
					height: 14px;
				}
			}
			main {
				padding-top: 0;
			}
		}
		.product-price {
			price-item {
				font-size: 14px;
				font-weight: 500;
			}
		}
	}
	.bundle-cart {
		padding: 15px;
	}
	.bundle-checklist {
		label {
			>span {
				width: auto;
				margin-bottom: 8px;
			}
		}
		.bundle-product-title {
			@include padding-end(8px);
		}
		.product-price {
			margin: 4px 0 0;
			price-item {
				font-size: 13px;
				font-weight: 500;
			}
		}
	}
}
@media (min-width: 750px) {
	.section-bundle-list {		
		.bundle-content {
			.card {
				border-bottom: 0;
				product-card {
					flex-direction: column;
				}
			}
			header {
				flex: auto;
				max-width: 100%;
				margin: 0;
			}
		}
		.bundle-submit button {
			width: auto;
		}
	}
	.product-layout-bundle {
		.second-column .product-info-inner {
			margin-bottom: 70px;
		}
		.product-info {
			product-card .card-title {
				font-size: 13px;
			}
			.bundle-checklist li {
				font-size: 13px;
			}
		}
		.main-product-info {
			>.lg-flex {
				flex-wrap: wrap;
			}
		}
	}
	.gallery-sticky {
		.main-product-gallery {
			position: sticky;
			top: 20px;
			align-self: flex-start;
		}
	}
}
@media (min-width: 1000px) {
	.section-bundle-list {		
		.bundle-content {
			display: flex;
			.bundle-list-item {
				flex: 1;
				>div:last-child {
					border: 0;
				}
				>div:nth-child(3) {
					border: 0;
				}
			}
			.bundle-cart {
				flex: 0 0 30%;
				@include border-inline-start(1px solid var(--miscellaneous-color));
			}
			.bundle-item {
				@include border-inline-end(1px solid var(--miscellaneous-color));
			}
		}
	}
}
@media (min-width: 1000px) {
	.product-layout-bundle {
		.main-product-info {
			flex: 0 0 50%;
		}
		.second-column {
			width: 100%;
		}
		.main-product-gallery {
			flex: 0 0 50%;
		}
	}
}
@media (min-width: 1200px) {
	.section-bundle-list {
		.bundle-content {
			.card {
				padding: 30px;
			}
		}
		.bundle-cart {
			padding: 30px;
		}
	}
	.product-layout-bundle {
		.main-product-info {
			flex: 0 0 59%;
		}
		.main-product-gallery {
			flex: 0 0 41%;
		}
		.has-wlcp-icon {
			.quantity-select {
				flex: 0 0 100%;
			}
		}
		.bundle-content {
			.card {
				padding: 20px;
			}
		}
		.bundle-cart {
			padding: 20px;
		}
		.main-product-info {
			.first-column {
				flex: 1;
			}
			.second-column {
				flex: 0 0 315px;
				max-width: 100%;
			}
		}
		.second-column .product-info-inner {
			@include padding-start(20px);
		}
	}
}